import { Box, Stack } from "@mui/material";
import React, { useEffect, useState } from "react";
import "./auth.scss";
import Input from "../../components/input";
import { Email, Password, Person } from "@mui/icons-material";
import Button from "../../components/button";
import cx from "classnames";
import { useNavigate } from "react-router-dom";
import useAuth from "../../store/auth";
import Papa from 'papaparse';
import configImage from '../../assets/svg/config.svg';
import csvFile from '../../assets/list.csv'
import { toast } from "react-toastify";


const AuthenticationPage = () => {
  const [tab, setTab] = useState(0);
  const [csvError,setCsvError] = useState(false)
  const [data, setData] = useState({
    username: "",
    password: "",
  });

  useEffect(() => {
    console.log("dot env file",process.env.REACT_APP_API_URL)
    fetchCSVData()
  }, [])

  async function fetchCSVData() {
    const response = await fetch(csvFile);
    const reader = response.body.getReader();
    const result = await reader.read();
    const decoder = new TextDecoder('utf-8');
    const csvText = decoder.decode(result.value);
    Papa.parse(csvText, {
      complete: (result) => {   
        const transformedData = result.data.reduce((acc, item) => {
          const [category, ...subcategories] = item;
          acc[category] = subcategories.filter(sub => sub !== "");
          return acc;
        },{});
      localStorage.setItem("oem",JSON.stringify(transformedData))
      },
      error: (error) => {
        toast.error('Error parsing CSV:',error)
        setCsvError(true)

      }
    });
  }
  const { login, register } = useAuth();

  const onChange = (e) => {
    if (!e.target) return;
    setData((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleAuth = () => {
    const { email, password, fullname, secret_key, username } = data;
    if (tab === 0) {
      login({ username, password });
    }
    if (tab === 1) {
      register({ email, password, fullname, secret_key, username });
    }
  };

  return (
    <Box className="auth-layout">
      <Box className="auth-card">
        <Stack style={{ height: "80vh", width: "100%" }} className="flex items-center justify-center">
          <img
            src={configImage}
            alt="auth icon"
          />
        </Stack>
        <Stack style={{ height: "81vh", width: '101%', backgroundColor: "#3a6490" }} className=" flex flex-col items-center  c-white">
          <h2 className="text-center">WELCOME</h2>
          <div className="flex items-center gap-20">
            <h3
              className={cx(
                "c-pointer"
              )}
            >
              LOGIN
            </h3>

          </div>
          <Stack sx={{ width: '90%', alignItems: 'center' }} className="signupScroll">
            {tab === 1 && (
              <Stack direction={{ sm: 'row' }} sx={{ alignSelf: 'flex-start' }} className="fieldWidth">
                <Input
                  required
                  title="Full Name"
                  name="fullname"
                  height="40px"
                  placeholder="Enter your full name"
                  // icon={<Person fontSize="small" />}
                  onChange={onChange}
                  value={data.fullname}
                  width={'86%'}
                // classes={"inputWidth"}
                />

                <Input
                  required
                  title="Username"
                  name="username"
                  height="40px"
                  placeholder="Enter your username"
                  // icon={<Person fontSize="small" />}
                  onChange={onChange}
                  value={data.username}
                  width={'86%'}
                // classes={"inputWidth"}
                />
              </Stack>
            )}
            <Input
              required
              title="Email"
              name="username"
              height="40px"
              placeholder="Please enter your email address"
              iconLeft
              icon={<Email fontSize="small" />}
              onChange={onChange}
              value={data.username}
              width={'86%'}
            />
            <Input
              required
              title="Password"
              name="password"
              height="40px"
              placeholder="Please enter your password"
              type="password"
              iconLeft
              icon={<Password fontSize="small" />}
              onChange={onChange}
              value={data.password}
              width={'86%'}
            />
            {tab === 1 && (
              <Input
                required
                title="Secret Key"
                name="secret_key"
                height="40px"
                placeholder="Please enter tenant's secret code"
                iconLeft
                icon={<Person fontSize="small" />}
                onChange={onChange}
                value={data.secret_key}
                width={'86%'}
              />
            )}
            <Button
              color="orange"
              onClick={()=>{if(csvError!== true){handleAuth()}}}
              classes="mt-5 mb-5 fw-600 uppercase"
            >
              Submit
            </Button>
          </Stack>
        </Stack>
      </Box>
    </Box>
  );
};

export default AuthenticationPage;
